$font_primary:'Montserrat', sans-serif;
$font_secondary:'Playfair Display', serif;
/* $f-l:300;
$f-r:400;
$f-m:500;
$f-sb:600;
$f-b:700; */
$primary-black:#000000;
$primary-white:#FFFFFF;
/* FONT-WEIGHT */
@mixin fontWeight($font-weight){
    font-weight:$font-weight;
}
.font-3{
    @include fontWeight(300);
}
.font-4{
    @include fontWeight(400);
}
.font-5{
    @include fontWeight(500);
}
.font-6{
    @include fontWeight(600);
}
.font-7{
    @include fontWeight(700);
}
.font-secondary{
    font-family: $font_secondary;
}
/* -------------------------- */
/* FONT-SIZE */
@mixin fontSize($font-size){
    font-size: $font-size;
}
.fs-22{
    @include fontSize(2.2rem);
}
.fs-20{
    @include fontSize(2.0rem);
}
.fs-30{
    @include fontSize(3.0rem);
}
.fs-18{
    @include fontSize(1.8rem);
}
.fs-40{
    @include fontSize(4.0rem);
}
.fs-25{
    @include fontSize(2.5rem);
}
.fs-16{
    @include fontSize(1.6rem);
}
.fs-33{
    @include fontSize(3.3rem);

    @media (max-width:1100px) {
        font-size:2.3rem;
    }
}
.fs-17{
    @include fontSize(1.7rem);
}
.fs-35{
    @include fontSize(3.5rem);
}
.fs-14{
    @include fontSize(1.4rem);
}
.fs-21{
    @include fontSize(2.1rem);
}
/* ------------------- */
/* flex properties */
.flex{
     
    display: flex;
    
}
.flex-wrap{
    display: flex;
    flex-wrap: wrap;
}
.flex-col{
    display: flex;
    flex-direction: column;
}
.justify-center{
  justify-content: center;
}
.justify-left{
    justify-content: left;
}
.align-center{
    align-items: center;
}
.justify-space-evenly{
    justify-content: space-evenly;
}
/* -------- */
/* PADDING */
/* padding */
.p-5{
    padding: 5px;
}
.p-10{
    padding: 10px;
}
.p-15{
    padding: 15px;
}
.p-20{
    padding: 20px;
}
.p-25{
    padding: 25px;
}
.p-30{
    padding: 30px;
}
/* padding-left */
.pl-5{
    padding-left: 5px;
}
.pl-10{
    padding-left: 10px;
}
.pl-15{
    padding-left: 15px;
}
.pl-20{
    padding-left: 20px;
}
.pl-25{
    padding-left: 25px;
}
.pl-30{
    padding-left: 30px;
}
/* padding-inline */
.px-5{
    padding-inline: 5px;
}
.px-10{
    padding-inline: 10px;
}
.px-15{
    padding-inline: 15px;
}
.px-20{
    padding-inline: 20px;
}
.px-25{
    padding-inline: 25px;
}
.px-30{
    padding-inline: 30px;
}
.px-70{
    padding-inline: 70px;
}
.py-8{
    padding-block:8px;
}