  .mobile, #nav-check {
    display: block;
  }

  .nav-btn-outer {
    width: 100%;
    color: #000;
    background-color: #fff;
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .nav-btn-inner {
    cursor: pointer;
    z-index: 2;
    transition: all .7s;
    display: block;
  }

  .nav-btn-inner.open {
    position: relative;
  }

  .nav-btn-inner.open span {
    opacity: 0;
  }

  .nav-btn-inner.open span:first-child {
    opacity: 1;
    transform: rotate(-45deg)translate(-5px, 6px);
  }

  .nav-btn-inner.open span:last-child {
    opacity: 1;
    transform: rotate(45deg)translate(-5px, -6px);
  }

  .nav-header {
    max-width: 100%;
    margin-block: 15px 17px;
    margin-inline: 42px;
    padding: 0;
  }

  .nav-header img {
    max-width: 100%;
  }

  .nav-btn-inner > label {
    text-align: center;
    width: 50px;
    height: 50px;
    padding: 13px;
    display: block;
  }

  span {
    width: 30px;
    height: 3px;
    background: #000;
    margin: 5px;
    transition: all .3s;
    display: block;
  }

  .nav-links {
    opacity: 1;
    z-index: 45;
    background-color: #181e28;
    justify-content: space-between;
    margin-bottom: 20px;
    font-size: 40px;
    position: absolute;
    right: 100%;
  }

  .nav-links .nav-link {
    font-weight: 600;
    font-size: 1.5rem !important;
  }

  .nav-link {
    color: #fff;
  }

  .menu {
    color: #171448;
    margin-left: 4px;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
  }

  .menu:hover {
    cursor: pointer;
  }

  .wrapper {
    height: 50px;
    justify-content: center;
    align-items: center;
    display: flex;
  }
