/* importhing font */
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&family=Playfair+Display:wght@700&display=swap");
@import "./src/sass/variables.scss";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  list-style: none;
  text-decoration: none;
}



html {
  font-size: 10px;
  @media (max-width:991px) {
    font-size:9px;
  }
  @media (max-width:768px) {
    font-size:8px;
  }
}
a,
button {
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.w {
  color: $primary-white;
}
body {
  background-color: $primary-white;
  font-family: $font_primary;
  color: $primary-black;
  margin: 0 auto;
}
.container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding-inline: 80px 70px;

  @media screen and (max-width767px) {
    max-width: 100%;
    padding-inline: 4%;
  }
}

.primary-btn {
  background: #181e28 0% 0% no-repeat padding-box;
  border-radius: 6px;
  color: $primary-white;
  font: 600 22px/27px "Montserrat";
  transition: all 300ms ease-in-out;
  padding: 9px 19px;
  display: block;
  &:hover {
    background-color: grey;
  }
 
}
header {
  width: 100%;
  z-index: 8;
 
}
.navbar{
  position: relative;
}
.nav-link {
  font-family: $font_primary;
  font-weight: 500;
  font-size: 1.7rem;
  color: $primary-black;
  letter-spacing: 0;
  line-height: 24px;
  transition: all 300ms ease-in-out;
  // padding-block: 4px;
  // padding-inline: 8px;
  padding: 8px 10px;
  @media (max-width: 1100px) {
 padding:5px 8px;
  }
}


.scrolling-active{
  .header-block{
    .nav-links {
      @media (max-width:991px) {
        position: fixed;
        top: 94px;
      }
    }
    .mobile{
      position: fixed;top: 0;
      left: 0;
      width: 100%;
      z-index: 999;
    }
  }
}

.header__inner-block {
  .active-menu{
    background-color: #f2aa52;
  }
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  .nav-header {
    width: 19%;
    @media (max-width: 1199px) {
      width: 15%;
    }
    @media (max-width: 1100px) {
      width: 12%;
    }
    @media (max-width:991px) {
    max-width: 271px;
    margin: 0 auto;
    width: 100%;
          }
    img {
      width: 100%;
      padding-block:15px;
    }
  }

  .nav-links {
    display: flex;
    justify-content: space-between;
    width: 58%;
    text-align: center;
    @media (max-width: 1400px) {
      width: 56%;
    }
    @media (max-width: 1199px) {
      width: 58%;
      
    }

    @media (max-width:991px) {
     opacity: 0;
     left: -100%;
     justify-content: space-between;
      background: rgba(52,73,94,0.9);
      position: absolute;
      top: 213px;
      // width: 100vw;
       height: calc(100vh - 94px);
      width: 100%;
      text-align: center;
      transition:all .4s ease;
      display: block;
      
    }
    a {
      display: block;
      @media (max-width: 1400px) {
        font-size: 1.4rem;
      }
      @media (max-width: 1199px) {
        font-size: 1.2rem;
      }
      @media (max-width: 1100px) {
        font-size: 1.1rem;
      }
    }
    &.active{


  
    opacity: 1;
 left: 0%;
 
  
    }
  }
  .btn-wrapper {
    a {
      @media (max-width: 1199px) {
        font-size: 18px;
        line-height: 22px;
      }
      @media (max-width: 1100px) {
        font-size: 14px;
        line-height: 16px;
        padding:5px 8px;
      }
      @media (max-width:991px) {
        display: none;
      }
    }
  }
}
