/* banner-section */
.banner {
  position:relative ;
  min-height: 644px;
  margin: 0 auto;
  background: url(./src/img/Group\ 126.png);
  background-size: 100%;
  max-width:100%;
  background-size: cover;
  margin-top: 0;
  padding-inline:70px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 150px;
  @media screen and (max-width: 1350px) {
    padding-bottom: 0;
   }
  
@media screen and (max-width: 991px) {
   padding-inline: 0;
  }
  @media screen and (max-width: 767px) {
    background: inherit;
  }
}
.banner-content {
  bottom: 0;
  margin-inline:auto;
  display: flex;
   align-items: end;
  align-content: end; 
  width:100%;
  justify-content: space-between;
  flex-wrap: wrap;
  @media screen and (max-width: 767px) {
    width: 100%;
    padding-inline:0;
  }

  h2 {
    font-family: $font_secondary;
    font-weight: 700;
    font-size: 3rem;
    color: $primary-white;
    letter-spacing: 0;
    line-height: 35px;
  }
  p {
    font-family: $font_primary;
    font-weight: 300;
    font-size: 1.8rem;
    color: $primary-white;
    letter-spacing: 0;
    line-height: 25px;
    margin-top: 10px;
  }
  h3 {
    font: normal normal bold 35px/50px Playfair Display;
    color: $primary-white;
    padding-bottom: 10px;
    text-align: left;
    margin-left: 10px;
  }
}

.banner-left {
  //position: relative;
  //bottom: -148px;
  width: calc(100% - 450px);
 // margin-block: 26em 7em;
  
  @media screen and (max-width: 991px) {
    width: 100%;
    background: url(./src/img/Group\ 126.png);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    gap: 220px;
    margin-top: 11em;
    padding-block: 25px;
    padding-inline: 16px;
    br {
      display: none;
    }
    margin-bottom: 0;
  }
  @media (max-width:991px) {
    margin-top:0;
  }
}

.baner-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 429px;
  @media screen and (max-width: 767px) {
    max-width: 100%;
    padding-inline: 20px;
    padding-block: 40px;
  }
  @media screen and (max-width: 575px) {
    padding-inline: 16px;
  }
}
.banner-right {
//position: relative;
//top:144px;
  width: 429px;
  @media (max-width:1542px) and (min-width:1350px){
    margin-bottom: -45px;
  }
  @media screen and (max-width: 767px) {
    width: 100%;
    background-color: #181e28;
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 0;
  }
}

.input {
  max-width: 429px;
  padding-left: 13px;
  padding-block: 10px;
  border-radius: 3px;
  &::placeholder {
    color: $primary-black;
    font-size: 14px;
    line-height: 18px;
    font-family: $font_primary;
  }
 
}
.input-text{
  height:81px;
}
.submit {
  width: 167px;
  background-color: #f2aa52;
  padding: 14px;
  color: $primary-black;
  border-radius: 3px;
  font-size: 1.7rem;
  line-height: 20px;
  font-weight: 700;
  transition: all 300ms ease-in-out;
  cursor: pointer;
  &:hover {
    background-color: #f9c98f;
    /*  color: #FFFFFF; */
  }
}

/* Our area section */
.heading {
  color: $primary-black;
  position: relative;

  h2 {
    font: normal normal medium 33px/40px Montserrat;
    padding-top: 40px;
    padding-left: 50px;
    @media screen and (max-width: 767px) {
    padding-left:0;
    
    }
  }
  h3 {
    font: normal normal bold 4.0rem/5.3rem Playfair Display;
    padding-left: 50px;
    @media screen and (max-width: 767px) {
      padding-left:0;
      
      }
  }

  &::before {
    width: 184px;
    height: 173px;
    content: "";
    background: #f2aa52;
    display: inline-block;
    position: absolute;
    z-index: -1;
    left: 0;
    @media screen and (max-width: 1038px) {
      height: 200px;
    }

    @media screen and (max-width: 767px) {
      display: none;
    }
  }
}
.practice-area {
  display: flex;
  gap: 40px;
  justify-content: baseline;
  @media screen and (max-width: 767px) {
    flex-direction: column;
    width: 100%;
  }
}
.practice-area-right {
  width: 50%;
  @media screen and (max-width: 767px) {
    width: 100%;
  }
  p {
    font-family: $font_primary;
    font-weight: 300;
    font-size: 18px;
    color: $primary-black;
    letter-spacing: 0;
    line-height: 30px;
    margin-top: 20px;
  }
}
.practice-area-left {
  width: 50%;
  @media screen and (max-width: 767px) {
    width: 100%;
    padding-inline: 0;
  }

  h2 {
    font-family: $font_secondary;
    font-weight: 700;
    font-size: 3rem;
    color: $primary-black;
    letter-spacing: 0;
    line-height: 48px;
    margin-top: 90px;
    padding-left: 50px;

    @media screen and (max-width: 767px) {
      margin-bottom: 70px;
      margin-top:30px;
      padding-left:0;
    }
  }
  h3 {
    font-family: $font_secondary;
    font-weight: 700;
    font-size: 2.5rem;
    color: $primary-black;
    letter-spacing: 0;
    line-height: 33px;
    padding-left: 90px;
    padding-block: 15px;
    padding-right: 5px;
  }
  p {
    font-family: $font_primary;
    font-weight: 300;
    font-size: 1.6rem;
    color: $primary-black;
    letter-spacing: 0;
    line-height: 23px;
    padding-left: 90px;
    padding-block: 15px;
    padding-right: 5px;
  }
}
.box-1 {
  border: 1px solid #c8c8c8;
  border-radius: 9px;
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  position: relative;
  margin-right: 30px;
  margin-block: 30px 15px;
  margin-left: 50px;
  @media screen and (max-width: 767px) {
    align-items: center;
    margin-top: 93px;
    margin-block: 0;
    padding: 5px;
    h3 {
      padding-left: 0;
      text-align: center;
      padding-top: 71px;
    }
    p {
      text-align: center;
      padding-left: 0;
      padding-top: 18px;
    }
    margin-inline: auto;
  }
}
.box-1-img {
  position: absolute;
  border: 1px solid #c8c8c8;
  padding: 22px;
  border-radius: 50%;
  left: -55px;
  width: 110px;
  background-color: #ffffff;
  /* box-sizing: content-box; */
}
.box-2 {
  border: 1px solid #c8c8c8;
  border-radius: 9px;
  display: flex;
  justify-content: center;
  align-items: start;
  flex-direction: column;
  position: relative;
  margin-right: 30px;
  margin-block: 15px 0;
  margin-left: 50px;
  @media screen and (max-width: 767px) {
    margin-block: 0;
    margin-top: 88px;
    padding: 5px;
    align-items: center;
    margin-inline: auto;
    h3 {
      padding-left: 0;
      text-align: center;
      padding-top: 71px;
    }
    p {
      text-align: center;
      padding-left: 0;
      padding-top: 18px;
    }
  }
}

.box-2-img {
  position: absolute;
  border: 1px solid #c8c8c8;
  padding: 22px;
  border-radius: 50%;
  left: -55px;
  width: 110px;
  background-color: #ffffff;
}
@media screen and (max-width: 767px) {
  .box-1-img,
  .box-2-img {
    top: -55px;
    left: 50%;
    margin-left: -55px;
  }
}

/* IMAGE CONTAINER SECTION */
.img-container-left {
  background-color: #181e28;
  width: 50%;
  @media screen and (max-width: 767px) {
    width: 100%;
    padding-inline: 4%;
  }

  h2 {
    line-height: 40px;
  }
  p {
    line-height: 30px;
    color: #ffffff;
    padding-bottom: 10px;
    padding-left: 11px;
    @media screen and (max-width: 767px) {
      padding-left: 0;
    }
  }
  .banner-2-heading {
    background-color: #f2aa52;
    margin-block: 46px 39px;
    padding-block: 20px;
    padding-left: 11px;
    padding-right: 53px;
    @media screen and (max-width: 767px) {
      padding: 6px;
    }
  }
}
.img-container {
  img {
    max-width: 100%;
    height: 100%;
  }
  max-width: 1600px;
  margin: 70px auto;
  object-fit: cover;
  @media screen and (max-width: 767px) {
    flex-direction: column-reverse;
    gap: 0;
  }
}
/* SIMPLE_BLOCK_SECTION */
.simple-block {
  max-width: 1600px;
  margin: 0 auto;
  padding-inline: 180px;
  @media screen and (max-width: 767px) {
    padding-inline: 4%;
  }
}
.simple-block-content {
  text-align: left;

  h2 {
    padding-block: 0 50px;
  }
  p {
    padding-bottom: 30px;
  }
}
/* TESTIMONIALS */
.testimonials {
  max-width: 100%;
  background-color: #f2aa52;
  margin: 0 auto;
}

.testimonials-container {
  max-width: 1600px;
  margin: 0 auto;
  margin-block: 20 80px;
  padding-bottom: 30px;

  img {
    max-width: 100%;
    height: 100%;
    object-fit: cover;
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
}
.containt {
  width: 100%;
  line-height: 38px;
  padding: 70px;
  padding-top: 0;

  @media screen and (max-width: 767px) {
    padding: 4%;
  }
  p {
    padding-block: 52px 40px;
    @media screen and (max-width: 767px) {
      padding-top: 25px;
    }
  }
}
.test-slide {
  width: 60%;
  @media screen and (max-width: 767px) {
    width: 100%;
  }
  @media (min-width: 768px) and (max-width: 1199px) {
    width: 100%;
  }
  h2 {
    margin-left: 70px;
    margin-top: 95px;
    line-height: 40px;
    @media screen and (max-width: 767px) {
      text-align: center;
      margin-left: 0;
      margin-top: 45px;
    }
  }
}
.owl-dots span {
  display: inline-block;
  border-radius: 50%;
  background-color: #f9c98f;
  width: 15px;
  height: 15px;
  margin: 4px;
  transition: all 300ms ease-in-out;

  &:active {
    cursor: pointer;
  }
}
.owl-dots .active span {
  background-color: #000000;
}
div.owl-dots {
  text-align: center;
}
.secondary-btn {
  display: inline-block;
  background-color: #181e28;
  color: #ffffff;
  padding: 14px 28px;
  border-radius: 3px;
  margin-top: 30px;
  transition: all 300ms ease-in-out;
  line-height: 20px;
  &:hover {
    background-color: grey;
  }
}
.ellipse {
  display: inline-block;
  border-radius: 50%;
  background-color: #f9c98f;
  width: 15px;
  height: 15px;
  margin: 4px;
  transition: all 300ms ease-in-out;

  &:hover {
    background-color: #000000;
  }
}
.testimonial-scroll {
  text-align: center;
  margin-top: 70px;
}

/* OUR TEAM SECTION */
.team {
  max-width: 1600px;
  margin: 0 auto;
  padding-inline: 70px 80px;
  h2 {
    text-align: center;
    padding-bottom: 50px;
  }
  @media screen and (max-width: 767px) {
    padding-inline: 4%;
  }
}
.cards {
  margin-bottom: 117px;
  gap: 40px;

  @media screen and (max-width: 767px) {
    column-gap: 42px;
  }
}
.card {
  width: 253px;
  min-height: 329px;
  transition: 300ms ease-in-out;
  border: 2px solid #E6E6E6;

  img {
    max-width: 100%;
  }
  h3 {
    padding-top: 25px;
    text-align: center;
    padding-bottom: 25px;
  }
  .default-card{
    border: 2px solid #f2aa52;
  }
  &:hover {
    border: 2px solid #f2aa52;
   
  }
  
}
/* contact us section */
.contact {
  max-width: 1600px;
  margin: 0 auto;
  padding-inline: 70px 80px;
  position: relative;
  @media screen and (max-width: 767px) {
    padding-inline: 4%;
  }
  img {
    display: block;
    @media screen and (max-width: 767px) {
      max-width: 100%;
    }
  }
}
.contact-us {
  background: transparent linear-gradient(180deg, #eeeeee 0%, #ffffff 100%) 0%
    0% no-repeat padding-box;
  img {
    max-width: 100%;
  }
}
.contact-card {
  text-align: left;
  background-color: #181e28;
  max-width: 442px;
  max-height: 268px;
  line-height: 40px;
  padding: 29px 27px 69px 29px;
  @media screen and (max-width: 1204px) {
    display: block;
    max-width: 100%;
    padding: 12px 26px 41px 16px;
    margin-bottom: 2.8em;
  }

  h2 {
    color: $primary-white;
  }
}
.contact-card-2 {
  /* display: inline-block; */
  max-width: 581px;
  padding-left: 20px;
  max-height: 143px;
  position: absolute;
  top: 47px;
  right: 95px;
  line-height: 28px;
  a {
    text-decoration: underline;
    color: inherit;
    &:hover {
      color: #808080;
    }
  }
  @media screen and (max-width: 1204px) {
    position: static;
    max-width: 100%;
    margin-bottom: 2.8em;
    max-height: fit-content;
    padding: 0;
    padding-bottom: 15px;
  }
}
.contact-card-3 {
  position: absolute;
  max-width: 595px;
  max-height: 227px;
  background-color: #f2aa52;
  right: 81px;
  top: 218px;
  padding: 34px 71px;
  @media screen and (max-width: 1204px) {
    position: static;
    max-width: 100%;
    margin-bottom: 2.8em;
    padding: 19px 40px 18px 16px;
    max-height: fit-content;
  }

  h2 {
    line-height: 53px;
  }
}

/* Logos Section */
.Companies-logo {
  max-width: 1600px;
  margin: 0 auto;
  @media screen and (max-width: 900px) {
    padding-inline: 0;
    margin-inline: 0;
  }
  img {
    padding-inline: 30px;
    max-width: 100%;
    object-fit: cover;
    @media screen and (max-width: 1472px) {
    }
  }
  @media screen and (max-width: 767px) {
    max-width: 100%;
    padding-inline: 4%;
  }
}
.logos {
  margin-block: 135px 78px;

  @media screen and (max-width: 1472px) {
    gap: 80px;
  }
  @media screen and (max-width: 922px) {
    justify-content: space-between;
    margin-bottom: 30px;
    padding-inline: 0;
    gap: 0;
    img {
      max-width: 75%;
    }
  }

  @media screen and (max-width: 767px) {
    flex-direction: column;
    gap: 48px;
    align-items: center;
  }
}
/* Footer SECTION */

.footer-bottom {
  background-color: #f2aa52;
}
.footer-bottom-containt {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 70px;
  padding-block: 14px 12px;
  @media screen and (max-width: 767px) {
    padding-left: 0;
    padding-inline: 4%;
  }
  p {
    line-height: 12px;

    padding-top: 4px;
    @media (max-width: 767px) {
      line-height: 21px;
    }
  }
  a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    &:hover {
      cursor: pointer;
    }
  }
}
/* FOOTER-TOP SECTION */
.footer-top {
  max-width: 1600px;
  margin: 0 auto;

  color: #ffffff;
  @media screen and (max-width: 767px) {
    max-width: 100%;
  }
}
.footer-top-containt {
  max-width: 1600px;
  padding-inline: 70px 250px;
  justify-content: space-between;
  padding-top: 74.27px;
  padding-bottom: 33px;
  gap: 100px;
  h2 {
    line-height: 27px;
  }
  h3 {
    line-height: 27px;
  }
  a {
    &:hover {
      cursor: pointer;
    }
  }
  @media screen and (max-width: 767px) {
    padding-left: 0;
    padding-inline: 4%;
    flex-direction: column;
    align-items: start;
    gap: 32px;
  }
}
.footer-para {
  max-width: 1600px;
  background-color: #181e28;
  color: #ffffff;
  margin: 0 auto;
  padding-left: 70px;
  @media screen and (max-width: 767px) {
    padding-inline: 4%;
  }
  p {
    padding-bottom: 27px;
    line-height: 19px;
  }
}
.social-icon {
  padding-block: 14px;
  gap: 8px;
}
.footer-container {
  background-color: #181e28;
}
/* END OF HOME PAGE CSS */

@media (max-width: 767px) {
  .container {
    max-width: 100%;
    padding-inline: 4%;
    margin: 0 auto;
  }
}
